export default {
	data() {
		return {}
	},
	onReady() {
		this.backHome();
	},
	methods: {
		//路由统一事件
		godetail: function(e) {
			if(e==0){
				uni.switchTab({
					url: ''
				});
			}else if(e==1){
				uni.switchTab({
					url: '/pages/Home/Index'
				});
			}
		},
		backHome : function() {
			setTimeout(() => {
				uni.switchTab({
					url: '/pages/Home/Index'
				});
			}, 10000);
		}

	}
}
